.faq {
    margin-top: 260px;
    &__title {
        font-size: 22px;
line-height: 32px;
text-align: center;
margin: 0;
    }
    &__subtitle {
        font-size: 16px;
line-height: 32px;
text-align: center;
margin: 0 0 25px;
display: block;
    }
    &__form {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 570px;
        margin: 0 auto;
    }
    &__input {
        max-width: 270px;
    }
    &__btn {
        margin-left: 30px;
        width: 270px;
        display: flex;
        justify-content: center;
    }
}